
import { defineComponent } from "vue";
import axios from "axios";

type RandomJokesData = {
  error: boolean;
  amount: number;
  jokes: {
    category: string;
    flags: {
      explicit: boolean;
      nsfw: boolean;
      political: boolean;
      racist: boolean;
      religious: boolean;
      sexist: boolean;
    };
    type: string;
    joke: string;
    id: number;
    safe: boolean;
    lang: string;
  }[];
};

export default defineComponent({
  name: "RandomJokes",
  data: () => ({
    loading: true,
    randomJokesData: {} as RandomJokesData,
  }),
  created() {
    axios
      .get("https://v2.jokeapi.dev/joke/Programming?type=single&amount=10")
      .then((response) => {
        this.randomJokesData = response.data;
        this.loading = false;
      });
  },
});
