
import { defineComponent } from "vue";
import axios from "axios";
import Button from "@/components/Buttons/Button.vue";

type RandomJokeData = {
  error: boolean;
  category: string;
  type: string;
  joke: string;
  flags: {
    explicit: boolean;
    nsfw: boolean;
    political: boolean;
    racist: boolean;
    religious: boolean;
    sexist: boolean;
  };
  id: number;
  safe: boolean;
  lang: string;
};

export default defineComponent({
  name: "RandomJokeInfo",
  components: { Button },
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data: () => ({
    loading: true,
    randomJokeData: {} as RandomJokeData,
  }),
  created() {
    axios
      .get(`https://v2.jokeapi.dev/joke/Any?idRange=${this.id}`)
      .then((response) => {
        this.randomJokeData = response.data;
        this.loading = false;
      });
  },
});
